* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
}

.app {
  background: url('./assets/cold.jpeg') no-repeat center;
  background-size: cover;
  transition: .2s ease-out
}

.app.warm {
  background: url('./assets/warm.jpeg') no-repeat center;
  background-size: cover;
  transition: .2s ease-out
}

main {
  min-height: 100vh;
  background: linear-gradient(to bottom, rgba(0,0,0,.3), rgba(0,0,0,.8));
  padding: 1.5rem;
}

.container {
  width: 90%;
  max-width: 500px;
  margin: 0 auto;
}

.main-heading {
  color: #fff;
  text-align: center;
  margin: 2rem 0;
  text-shadow: 2px 4px rgba(50,50,70,.5);
  font-size: 2.5rem;
}

.search-box {
  width: 100%;
  margin: 0 0 4rem;
}

.search-bar {
  display: block;
  width: 100%;
  padding: 1rem;
  appearance: none;
  background: rgba(255,255,255,.3);
  border-radius: 5px;
  border: none;
  outline: none;
  color: #fff;
  font-size: 1.5rem;
  box-shadow: 0 6px 18px rgba(0,0,0,.2);
  transition: .2s ease;
}

.search-bar::placeholder {
  color: #bbb
}

.search-bar:focus {
  background: rgba(255,255,255,.4);
}

.location {
  color: #fff;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 6px rgba(50,50,70,.5);
}

.date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 4px rgba(50,50,70,.5);
}

.weather-box {
  text-align: center;
}

.temp {
  position: relative;
  display: inline-block;
  margin: 2rem auto;
  background-color: rgba(255,255,255,.2);
  border-radius: 1rem;
  padding: 1rem 2rem;
  color: #fff;
  font-size: 6rem;
  font-weight: 800;
  text-shadow: 3px 6px rgba(50,50,70,.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,.2);
}

.weather {
  color: #fff;
  font-size: 3rem;
  font-weight: 700;
  text-shadow: 3px 6px rgba(50,50,70,.5);
}
